import React from 'react';
import View from 'components/main/pages/home/views/View';
import ImagePicker from 'components/main/items/ImagePicker';

class AddProject extends View {

  constructor(props){
    super(props);
    this.init(props);
    this.state = {
      modified: false,
      url: null
    }
    this.project = this.store.projects.viewingProject;
    this.getIconUrl(props);
  }

  componentWillReceiveProps(newProps){
    this.init(newProps);
    this.getIconUrl(newProps);
  }

  async getIconUrl(props){
    if(!this.props.editMode || this.state.url){ return; }
    const url = await this.func.url(this.project.icon, 'projectIcon');
    this.setState({
      url: url
    });
  }

  render() {
    this.init(this.props);
    var defaultDate = new Date();
    defaultDate.setMonth(defaultDate.getMonth() + 1);

    return(
      <div style={this.viewStyle()}>
        {this.gap('4%')}

        {this.subTitle(['Icon',''])}
        {this.sep()}
        <ImagePicker defaultUrl={this.state.url} app={this.app} />
        {this.sep()}
        {this.gap('2%')}

        {this.subTitle(['Title',''])}
        {this.sep()}
        {this.inputs.inputField('title','text', '', this.props.editMode? this.project.title:'' , ()=>{ this.setState({modified: true})})}
        {this.gap('2%')}

        {this.subTitle(['Description',''])}
        {this.sep()}
        {this.gap('2%')}
        {this.inputs.textArea('desc', '', this.props.editMode? this.project.description:'', ()=>{ this.setState({modified: true})})}
        {this.gap('4%')}

        {this.subTitle(['End date',''])}
        {this.sep()}
        {this.inputs.inputField('endDate','date', ['',''], this.func.getDateString(this.props.editMode? new Date(this.project.endDate):defaultDate) , ()=>{ this.setState({modified: true})})}
        {this.gap('2%')}

        {this.buttons.rectGreen(['Confirm',''], ()=>{this.addProject()})}
      </div>
    )
  }

  addProject(){
    const editMode = this.props.editMode;
    const newIconBlob = this.store.main.photoBlob;
    const title = document.getElementById('title').value;
    const desc = document.getElementById('desc').value;
    const endDate = document.getElementById('endDate').value;

    const today = new Date();
    const selectedEndDate = new Date(endDate)
    if(!editMode && newIconBlob === null){
      return this.failedMessage(['Failed to add! Icon is missing!', '! !'])
    }
    if(title.length === 0){
      return this.failedMessage(['Failed to add! Title is missing!', '! !'])
    }
    if(selectedEndDate < today){
      return this.failedMessage(['Failed to add! End date is in the past!', '! !'])
    }

    if(!editMode){
      this.actions.projects.addProject({
        course: this.store.courses.viewingCourse._id,
        icon: newIconBlob,
        title: title,
        description: desc,
        endDate: endDate
      });
    }else if(newIconBlob || this.state.modified){
      this.actions.projects.editProject({...this.project, ...{
        newIcon: newIconBlob,
        title: title,
        description: desc,
        endDate: endDate
      }})
    }else{
      return this.failedMessage(['Failed to add! Nothing is modified!', '!!'])
    }
  }

}

export default AddProject;



